<template>
  <!--购物车订单-->
  <div class="createOrder left">
    <home-header :title="'订单确认'" :back="true"></home-header>
    <div class="create-main">
      <div class="create-item create-main-address" @click="$router.push({name:'addressList',query:{goto:'2'}})">
        <div class="create-item-title">
          收货地址：
        </div>
        <div class="create-address" v-if="addressInfo">
          <div class="create-address-name">
            <p>收货人：{{addressInfo.receiverName}}</p>
            <p>手机：{{addressInfo.receiverMobile}}</p>
          </div>
          <div class="create-address-content">
            {{addressInfo.receiverProvince}}省{{addressInfo.receiverCity}}{{addressInfo.receiverDistrict}}{{addressInfo.receiverAddress}}
          </div>
        </div>
        <div class="create-address" v-else>
          您还未添加收获地址，请您添加收货地址
        </div>
      </div>
      <div class="create-item create-main-pay">
        <div class="create-item-title create-top-arrows" @click="payShow = !payShow">
          支付方式：<i class="iconfont icon-jiantouxia" :class="{active:payShow}"></i>
        </div>
        <div class="create-item-content clear" v-show="payShow">
          <i class="iconfont icon-danduduihao"></i><span>易宝支付</span>
        </div>
      </div>
      <div class="create-item create-main-message">
        <div class="create-item-title create-top-arrows" @click="messageShow = !messageShow">
          订单附言：<i class="iconfont icon-jiantouxia" :class="{active:messageShow}"></i>
        </div>
        <div class="create-item-input" v-show="messageShow">
          <mt-field placeholder="请输入订单留言" type="textarea" rows="2" v-model="introduction"
                    :attr="{ maxlength: 50 }"></mt-field>
        </div>
      </div>
      <div class="create-item create-main-product" v-if="productLi">
        <div class="create-product-title">
          商品列表
        </div>
        <div class="create-product-item">
          <div class="create-product-list" v-for="(item,index) in productLi.cartProductDtoList" :key="index">
            <div class="create-product-image">
              <img :src="imgHost+item.img" alt="">
            </div>
            <div class="create-product-content">
              <div class="create-product-name ellipsis">
                {{item.productName}}
              </div>
              <div class="create-product-count">
                数量：{{item.quantity}} <span class="total-count">&nbsp;{{item.productText | filterParse}}</span>
              </div>
              <div class="create-product-money">
                价格：<span class="total-money">￥{{item.productPrice}}元</span>
              </div>
            </div>
          </div>
        </div>
        <div class="create-product-collect">
          <p>商品总价：<span>￥{{productLi.cartTotalPrice}}元</span></p>
          <p v-if="false">快递费：<span>￥{{}}元</span></p>
          <p>应付费用：<span>￥{{productLi.cartTotalPrice}}元</span></p>
        </div>
      </div>
      <div class="create-main-btn">
        <span @click="submitOrder">订单提交</span>
      </div>
    </div>
    <mt-popup v-model="payPopupShow" position="bottom" class="pay-mt-popup">
      <div class="pay-popup">
        <div class="pay-title">
          <div class="pay-title-cancel" @click="payPopupShow = false">
            取消
          </div>
          确认付款
        </div>
        <div class="pay-content">
          <div class="pay-money">
            <div class="pay-money-title">付款金额</div>
            <div class="pay-money-val">￥{{paymentActual}}</div>
            <div class="pay-money-refer">￥{{payment}}</div>
          </div>
          <div class="pay-price">
            <div class="pay-price-title">优惠金额</div>
            <div class="pay-price-count">￥{{actualMoney}}</div>
          </div>
          <div class="pay-type">
            <div class="pay-type-title">付款方式</div>
            <div class="pay-type-content1" @click="bindBankCard()" v-if="bindCard==1">
              <span>您还没有绑定银行卡</span><i class="iconfont icon-arrow-right-copy"></i>
            </div>
            <div class="pay-type-content" v-if="bindCard==2" @click="addActive=true">
              <div class="pay-type-img">
                <img src="../../assets/img/ABC.png" alt="">
              </div>
              <div class="pay-type-right">
                <div class="type-right-title">
                  {{bankNum}}
                </div>
                <div class="type-right-num">
                  {{bankType}}
                </div>
              </div>
            </div>
          </div>
          <div class="pay-type-button">
            <div class="pay-type-btn" @click="rightPay()">确认支付</div>
          </div>
          <div class="pay-type-notice">
            支持银行卡支付
          </div>
        </div>
      </div>
      <!--添加银行卡-->

      <div class="pay-popup popup-add-pay" :class="addActive?'moveLeft':'moveRight'">
        <div class="pay-title">
          <div class="pay-title-cancel" @click="addActive=false">
            返回
          </div>
          选择付款方式
        </div>
        <div class="pay-content">
          <div class="pay-type">
            <div class="pay-type-content" v-for="item in bankList">
              <div class="pay-type-img">
                <img src="../../assets/img/ABC.png" alt="">
              </div>
              <div class="pay-type-right">
                <div class="type-right-title">
                  {{item.cardNo}}
                </div>
                <div class="type-right-num">
                  {{item.bankName}}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="pay-add-code">
          使用新卡支付
        </div>
      </div>
    </mt-popup>

  </div>
</template>

<script>
  import homeHeader from '../../components/header/header.vue'
  import {Toast} from 'mint-ui'
  import {filterParse} from '../../mixins/filter'

  export default {
    data() {
      return {
        bindCard: null,
        payPopupShow: false,
        messageShow: false,
        payShow: false,
        addActive: false,
        introduction: '',
        bankList: [],
        bankNum: '',
        bankType: '',
        orderNo: '',
        bindId: '',
        payment: '',
        paymentActual: '',
        bankCode: '',
        actualMoney: '',
        addressInfo: null,
        productLi: null,
        shippingId:'',
      }
    },
    computed: {},
    components: {
      homeHeader
    },
    filters: {
      filterParse
    },
    methods: {
      getAddress() {
        let that = this;
        let memberId = window.sessionStorage.getItem('tempMemberId') ? window.sessionStorage.getItem('tempMemberId') : window.localStorage.getItem('memberId');
        that.ajax({
          url: '/shipping/get/one',
          method: 'post',
          data: {
            memberId: memberId
          },
          success: function (res) {
            if (res.status == 1000) {
                that.addressInfo = res.data;
                that.shippingId = res.data.id;
            }
          }
        })
      },
      createInfo() {
        let that = this;
        let memberId = window.sessionStorage.getItem('tempMemberId') ? window.sessionStorage.getItem('tempMemberId') : window.localStorage.getItem('memberId');
        that.ajax({
          url: '/cart/list?memberId=' + memberId,
          success: function (res) {
            that.productLi = res.data;
          }
        })
      },
      submitOrder() {
        let that = this;
        //生成订单  判断是否有默认地址，没有无法生成订单
        if (that.addressInfo) {
          that.ajax({
            url: '/order/from/cart',
            method: 'post',
            data: {
              memberId: window.localStorage.getItem('memberId'),
              shippingId: that.shippingId,
              productText: ''
            },
            success: function (res) {
              console.log(res.data.payment + '原价')
              that.payment = res.data.payment;
              that.paymentActual = res.data.paymentActual;
              that.actualMoney = that.payment - that.paymentActual;
              that.orderNo = res.data.orderNo;
              that.payPopupShow = true;
            }
          }),
            //银行卡列表
            that.ajax({
              url: '/pay/cardlist',
              method: 'post',
              success: function (res) {
                console.log(res.data.code)
                var authouer = res.data.code;
                var cardList = res.data.cardList;
                console.log(authouer)
                if (authouer == '1') {
                  //设置默认展现银行卡
                  that.bindCard = 2;
                  that.bankType = cardList[0].bankName;
                  that.bankNum = cardList[0].cardNo;
                  that.bindId = cardList[0].bindId;
                  that.bankCode = cardList[0].bankCode;
                  that.bankList = cardList;
                } else {
                  that.bindCard = 1;
                }
              }
            })
        } else {
          Toast('您还未添加收获地址，请您添加收获地址')
        }
      },
      bindBankCard() {
        let that = this;
        that.ajax({
          url: '/pay/authUser?returnUrl=http://localhost:8080/createOrder',
          method: 'post',
          success: function (res) {
            var status = res.body.code;
            var redirectUrl = res.body.redirectUrl
            console.log(res.body.code);
            if (status != 'US2016') {
              window.location.href = redirectUrl
            }
          }
        })
      },
      rightPay() {  //立即支付
        let that = this;
        that.ajax({
          url: '/pay/consume?bindCardId=' + that.bindId + '&bankCode=' + that.bankCode + '&id=' + that.orderNo + '&webCallbackUrl=http://localhost:8080/result&payment=0.1&paymentActual=0.1',
          method: 'post',
          success: function (res) {
            var code = res.data.code;
            var redirectUrl = res.data.redirectUrl;
            if (code == 1) {
              window.location.href = redirectUrl;
            }
          }
        })
      }
    },
    watch: {
      payPopupShow: function (newVal) {
        if (!newVal) {
          this.addActive = false;
          this.$router.push({name: 'waitPay', query: {goback: 'proDetail'}});
        }
      }
    },
    created() {
      this.getAddress();
      this.createInfo();
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/minxin";

  .create-main {
    padding-top: $top;
  }

  //公共样式
  .create-item {
    background: $f;
    border-top: 1px solid $border;
    border-bottom: 1px solid $border;
    margin: 5px 0;
    padding: 0 10px;
    position: relative;
    .create-item-title {
      padding: 10px 0;
    }
  }

  .create-main-address {
    padding-right: 30px;
    //右侧箭头
    &:after {
      display: block;
      @include wh(10px, 10px);
      border-top: 2px solid $border;
      border-right: 2px solid $border;
      transform: translateY(-50%) rotateZ(45deg);
      position: absolute;
      top: 50%;
      right: 15px;
      content: '';
    }
    .create-address {
      padding: 10px;
      @include sc($med, $gray);
      .create-address-name {
        @include fj;

      }
      .create-address-content {
        word-break: break-all;
        @include sc($min, $grey);
        padding: 5px 0 10px;
      }
    }
  }

  //上下箭头
  .create-top-arrows {
    position: relative;
    .iconfont {
      position: absolute;
      right: 5px;
      @include sc(7px, $border);
      transform: rotateZ(0deg);
    }
    .active {
      transform: rotateZ(180deg);
    }
  }

  .create-main-pay {
    .create-item-content {
      padding-left: 10px;
      padding-bottom: 10px;
      line-height: 1em;
      .iconfont {
        display: block;
        float: left;
        background: $red;
        border-radius: 100%;
        color: $f;
        padding: 1px;
      }
      span {
        float: left;
        padding-left: 10px;
      }
    }
  }

  .create-main-message {
    .create-item-input {
      border: 1px solid $border;
      margin-bottom: 10px;
    }
  }

  .create-main-product {
    .create-product-title {
      padding: 10px 0;
      border-bottom: 1px solid $border;
    }
    .create-product-item {

    }
    .create-product-list {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      border-bottom: 1px solid $border;
      padding: 10px 0;
      &:last-child {
        border: 0;
      }
      .create-product-image {
        @include wh(6rem, 6rem);
        img {
          @include wh(6rem, 6rem);
        }
      }
      .create-product-content {
        padding-left: 10px;
        .create-product-name {
          @include sh($max, 1.3em);
          height: 2.6em;
        }
        .create-product-count, .create-product-money {
          @include sc($min, $grey);
          padding-top: 5px;
          .total-count {
            color: $gray;
          }
          .total-money {
            color: $red;
          }
        }
      }
    }

  }

  .create-product-collect {
    border-top: 1px solid $border;
    padding: 10px 0;
    p {
      text-align: right;
      @include sh($min, 1.5em);
    }
    span {
      color: $red;
    }
  }

  .create-main-btn {
    margin: 10px;
    span {
      display: block;
      @include wh(100%, 2.6em);
      @include sc($med, $f);
      line-height: 2.6em;
      text-align: center;
      background: $red;
      border-radius: 5px;
    }
  }

  $border: #ccc;
  $size: 1rem;

  /*支付弹窗*/
  .mint-popup {
    height: 75%;
    font-family: "Microsoft Yahei";
    font-weight: 100;
    .pay-popup {
      height: 100%;
      background: #F8F8F8;
      width: 100vw;
      .pay-title {
        border-bottom: 0.5px solid $border;
        text-align: center;
        @include sh($max, 3.6rem);
        position: relative;
        .pay-title-cancel {
          position: absolute;
          left: 0;
          top: 0;
        }
      }
      .pay-content {
        padding: 0 0.5rem;
        .pay-money {
          text-align: center;
          border-bottom: 1px solid $border;
          .pay-money-title {
            @include sh(1.2rem, 2.2em);
          }
          .pay-money-val {
            @include sh(1.4rem, 2.8rem);
          }
          .pay-money-refer {
            @include sh(0.9rem, 2.6rem);
            color: #bbb;
            text-decoration: line-through;
          }
        }
        .pay-price {
          @include fj;
          flex-direction: row;
          border-bottom: 0.5px solid $border;
          @include sh($size, $min*3);
          .pay-price-count {
            color: red;
          }
        }
        .pay-type {
          .pay-type-title {
            @include sh(0.98rem, 0.98rem);
            padding-top: 1rem;
          }
          .pay-type-content1 {
            display: flex;
            display: -webkit-flex;
            justify-content: space-between;
            padding: 1rem 0.6rem 0.6rem 0;
            font-size: 18px;
            i {
              font-size: 20px;
            }
          }
          .pay-type-content {
            display: flex;
            flex-direction: row;
            padding: 1rem 0 0.6rem;
            border-bottom: 1px solid $border;
            .pay-type-img {
              padding-left: 1rem;
              @include wh(3.8rem, 2.8rem);
              img {
                @include wh(2.6rem, 2.6rem);
              }
            }
            .pay-type-right {
              display: flex;
              flex-direction: column;
              padding-left: 1.6rem;
              flex: 1;
              justify-content: space-between;
              .type-right-title, .type-right-num {
                font-size: $size;
              }
            }
          }
        }
        .pay-type-button {
          padding: 1.2rem 0.5rem 0.2rem;
          .pay-type-btn {
            @include wh(100%, 3rem);
            @include sh(1.1rem, 3rem);
            border: 0.5px solid #00a0e9;
            color: #00a0e9;
            border-radius: 5px;
            text-align: center;
            font-weight: 100;
          }
        }
        .pay-type-notice {
          text-align: center;
          @include sc($min, $gray);
        }
      }
      /*选择付款方式*/
      .pay-content {
        min-height: 30vh;
      }
      .pay-add-code {
        @include sh(1.24rem, 4rem);
        background: $f;
        text-align: center;
        font-weight: 100;
      }

    }
  }

  .pay-mt-popup {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .popup-add-pay {
      height: 100%;
      position: absolute;
      left: 100vw;
      top: 0;
    }
    .moveLeft {
      transform: translateX(-100%);
      transition: transform 0.3s;
    }
    .moveRight {
      transform: translateX(0);
      transition: transform 0.3s;
    }
  }

</style>



